.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


header .social li a {
  color: peru;
}

.portfolio-item-meta {
  min-height: 80px;
  max-height: 80px;
}

.portafolio-image {
  min-width: 40px;
  min-height: 40px;
  background-color: darkslategray;
}

a.link-open {
  padding: 10px;
  border-width: 1px;
  border-color: aqua;
  background-color: rgb(209, 209, 209);
  color: rgb(248, 123, 21) !important;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
